import React, { createContext, useReducer, useEffect, useContext } from "react";

// try-catch
let apiDataJson = [];
try {
  // JSON
  apiDataJson = require("../../apiData.json");
} catch (error) {
  console.warn("apiData.json", error);
  apiDataJson = [];
}

// 
const initialState = {
  apiData: [],
  loading: true,
  error: null,
  errorDetails: null,
};

// actions
const ACTION_TYPES = {
  FETCH_SUCCESS: "FETCH_SUCCESS",
  FETCH_ERROR: "FETCH_ERROR",
  SET_LOADING: "SET_LOADING",
};

// reducer
function apiReducer(state, action) {
  switch (action.type) {
    case ACTION_TYPES.FETCH_SUCCESS:
      return {
        ...state,
        apiData: action.payload,
        loading: false,
        error: null,
      };
    case ACTION_TYPES.FETCH_ERROR:
      return {
        ...state,
        loading: false,
        error: action.payload.message,
        errorDetails: action.payload.details,
      };
    case ACTION_TYPES.SET_LOADING:
      return {
        ...state,
        loading: action.payload,
      };
    default:
      return state;
  }
}

// 
const ApiDataContext = createContext();

// 
export const ApiDataProvider = ({ children }) => {
  const [state, dispatch] = useReducer(apiReducer, initialState);

  useEffect(() => {
    // JSON
    try {
      console.log(` ${apiDataJson.length} API`);

      // responseExample
      const processedData = apiDataJson.map((api) => {
        // responseExampleJSON
        if (
          api.responseExample &&
          (typeof api.responseExample === "object" ||
            Array.isArray(api.responseExample))
        ) {
          return {
            ...api,
            responseExample: JSON.stringify(api.responseExample, null, 2),
          };
        }
        return api;
      });

      dispatch({
        type: ACTION_TYPES.FETCH_SUCCESS,
        payload: processedData,
      });
    } catch (err) {
      console.error("API:", err);
      dispatch({
        type: ACTION_TYPES.FETCH_ERROR,
        payload: {
          message: "API",
          details: err,
        },
      });
    }
  }, []);

  // 
  return (
    <ApiDataContext.Provider value={{ ...state, dispatch }}>
      {children}
    </ApiDataContext.Provider>
  );
};

// 
export const useApiData = () => {
  const context = useContext(ApiDataContext);
  if (!context) {
    throw new Error("useApiData must be used within an ApiDataProvider");
  }
  return context;
};

// actiondispatch
export const apiActions = {
  setLoading: (isLoading) => ({
    type: ACTION_TYPES.SET_LOADING,
    payload: isLoading,
  }),
  fetchSuccess: (data) => ({
    type: ACTION_TYPES.FETCH_SUCCESS,
    payload: data,
  }),
  fetchError: (error) => ({
    type: ACTION_TYPES.FETCH_ERROR,
    payload: { message: error.message, details: error },
  }),
};

export default ApiDataContext;
